import http from "./http.js";

export default {
    // 人员黑名单查询
    getPersonBlack(params) {
        return http.get("/v1/personBlack", params);
    },
    // 车辆黑白名单查询
    getVehicle(params) {
        return http.get("/v1/vehicle",params)
    }
};
